























import Api from '../../api/api.js'
import Fetch from '../../util/fetch.js'
import split from '../../components/split.vue'
import sift from '../../components/sift/sift.vue'
export default {
	components:{
		split,
		sift
	},
	data() {
		return {
			type: '',
			bannerInfo: '',
			goods: [],
			page: 1,
			size: 10,
			order: 'desc',
			sort: 'default',
			categoryId: 0
		};
	},
	onLoad(options) {
		this.type = options.type;
		if(!this.type) uni.navigateBack();
	},
	methods:{
		valueChanged(val){
			this.categoryId = 0;
			this.sort = 'default';
			this.order = 'desc';
			if(val.name === "综合"){
				
			} else if(val.name === "价格") {
				this.sort = 'price';
				this.order = val.order === 1 ? "asc":"desc";
			} else if(val.name === '分类') {
				this.sort = 'category';
				this.categoryId = val.index;
			}
			this.getGoods();
		},
		getGoods(){
			const that = this;
			let params = {};
			params['page'] = this.page;
			params['size'] = this.size;
			params['order'] = this.order;
			params['categoryId'] = this.categoryId;
			params['sort'] = this.sort;
			if(this.type === 'new'){
				params['isNew'] = 1;
			} else if(this.type === 'hot') {
				params['isHot'] = 1;
			} else {}
			Fetch.fetchData(Api.GoodsList,params,(res)=>{
				const data = res.data.data;
				that.goods = data.data;
			},true);
		}
	},
	mounted() {
		const that = this;
		Fetch.fetchData(this.baseUrl,null,(res)=>{
			const data = res.data.data;
			that.bannerInfo = data.bannerInfo;
			that.getGoods();
		},true);
	},
	computed:{
		config(){
			if(this.type == 'new'){
				return [
					{
						type: 0,
						name: '综合',
						values:[]
					},
					{
						type: 1,
						name: '价格',
						values:[]
					},
					{
						type: 2,
						name: '分类',
						values:['全部','居家','餐厨','婴童','杂货','饮食']
					}
				]
			} else {
				return [
					{
						type: 0,
						name: '综合',
						values:[]
					},
					{
						type: 1,
						name: '价格',
						values:[]
					},
					{
						type: 2,
						name: '分类',
						values:['全部','居家','配件','饮食','志趣']
					}
				]
			}
		},
		baseUrl () {
			if(this.type === "new"){
				return Api.GoodsNew
			} else if(this.type === "hot"){
				return Api.GoodsHot
			} else {
				return ''
			}
		}
	}
}
